import { useState } from 'react';
import { Link } from 'react-router-dom'
import data from '../data';

function ExData(props){
    let [shoes] = useState(data)
    return(
    <div className="container">
        <div className="row">
            {
              shoes.map((product, index) => (
                <ProductDisplay id={product.id} title={product.title} price={product.price} image={product.id+1}/>
              ))  
            }
            
          </div>    
    </div> 
    )
}
// 메인-상품전시
function ProductDisplay(props){
  return(
    <div className="col-md-4">
      <Link to={"/detail/"+props.id}>
      <img src={ "https://codingapple1.github.io/shop/shoes"+props.image+".jpg"} width="80%" />
      </Link>
      <h4>{ props.title }</h4>
      <p>{ props.price }</p>
    </div>  
  )
}

export default ExData;